<template>
    <div class="data-content-layout">
        <header class="data-content-topbar clearfix">
            <a @click="goPage()" class="datadl-content-back" v-show="isShowBak"></a>
            <h4 class="data-content-title">
                文档阅览
            </h4>
        </header>
        <article class="data-content-article">
            <h2 class="article-title">
                内容摘要
            </h2>
            <div class="article-content">
                <p class="article-paragraph">
                    {{abstract}}
                </p>
            </div>
            <div class="datajifen-download">
                <div class="datajifen-dialog">
                    <h2 class="datajifen-need-title">您当前的积分为{{currentCredit}}积分</h2>
                    <h2 class="datajifen-need-title">如需要观看全文，需使用 {{credit}}积分</h2>
                    <a @click="goGetJiFenPage($event)" class="datajifen-how-get">
                        如何获得积分？
                    </a>
                    <a @click="openwin(url)" class="datajifen-download-btn" id="dow">
                        下载
                    </a>
                    <div class="datajifen-watch clearfix">
                        <div class="watch-l">
                            <!--<img src="../../../src/assets/images/datajifen-eyes.png" alt="" class="eyes">
                            <span class="watch-num">
                                120
                            </span>-->
                        </div>
                        <div class="watch-r">
                            <img src="../../../src/assets/images/datajifen-download-icon.png" alt="" class="datajifen-d">
                            <span class="download-num">
                                {{downloadCount}}
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </article>
    </div>
</template>
<script>
  import Api from '../../api/api';
  import logUtil from '../../utils/log';
  import {getSignInfo} from '../../api/tiezidetail';
  import wx from 'weixin-js-sdk';
  export default{
      data(){
        return{
            id:-1,
            //文件的摘要
            abstract:'',
            //下载该文件所需要的积分
            credit:0,
            //用户当前积分
            currentCredit:0,
            uploader:-1,
            downloadCount:0,
            url:"",
            isShowDataBase:false,
            isShowBak:true
        }
      },
      mounted(){
          this.$nextTick(function () {
            if(this.isWeiXin()){
              this.isShowBak = false;
              this.getAsign();
            }else{
              this.isShowBak = true;
              this.getUserJIfen();
              this.getDatumById(this.$route.params.fileId);
            }

          });
      },
    watch:{
      $route(to){
        //对传递的id进行验证(防止篡改)
        let reg=/datajifendl\/\d+/;
        if(reg.test(to.path)){
          let newsId=this.$route.params.fileId || 0;
          this.getDatumById(newsId);
        }
      }
    },
      methods:{
          goPage(){
              if(this.isShowDataBase){
                window.location.href = "http://m.ciyigou.com/mobile/index#/database";
              }else{
                window.history.go(-1);
              }
          },
        isWeiXin(){
          //判断是否是微信浏览器
          let ua = window.navigator.userAgent.toLowerCase();
          if (ua.match(/MicroMessenger/i) == 'micromessenger') {
            return true;
          } else {
            return false;
          }
        },
        getAsign(){
          const _this = this;
          //将这个动态的url传递给后端(便于后端进行签名操作)
          let url = encodeURIComponent(window.location.href.split('#')[0]);
          //请求分享获取到的签名信息
          getSignInfo(Api.SIGN,url).then((res)=>{
            _this.initAsign(res);
          },(err)=>{
          });
        },
        initAsign(res){
          const _this = this;
          if(res.status === 200){
            //从服务器端获取到必要的参数,将必要的信息进行初始化赋值
            let sinObj = res.data.data.detail;
            wx.config({
              debug: false,
              appId: sinObj.appId, // 和获取Ticke的必须一样------必填，公众号的唯一标识
              timestamp:sinObj.timestamp.toString(), // 必填，生成签名的时间戳
              nonceStr: sinObj.nonceStr, // 必填，生成签名的随机串
              signature: sinObj.signature,// 必填，签名，见附录1
              //需要分享的列表项:发送给朋友，分享到朋友圈，分享到QQ，分享到QQ空间
              jsApiList: [
                'onMenuShareAppMessage','onMenuShareTimeline',
                'onMenuShareQQ','onMenuShareQZone'
              ]
            });
            wx.ready(function () {
              _this.getUserJIfen();
              _this.getDatumById(_this.$route.params.fileId);
            });
          }
        },
          getDatumById(id){
            this.$http.get(`${Api.FILES}${id}/`).then((res)=>{
                const _this = this;
              logUtil.printLog("获取到:",res);
              this.id = res.data.data.detail.id;
              this.abstract = res.data.data.detail.abstract;
              this.credit = res.data.data.detail.credit;
              this.uploader = res.data.data.detail.uploader;
              this.downloadCount = res.data.data.detail.download_count;
              this.url = res.data.data.detail.file_url;
              var str = window.location.href;
              var regEn1 = /[?]/im;
              if(regEn1.test(str) ) {
                var arr = str.split("?")[0];
                var arr1 = str.split("?")[1].split("#")[1];
                str = arr+"#"+arr1;
              }
              wx.onMenuShareTimeline({
                title: res.data.data.detail.file_name, // 分享标题
                link: str, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                imgUrl: res.data.data.detail.file_image, // 分享图标
                success: function (res) {
                  _this.isShowDataBase =true;
                  _this.showMsg("分享成功!")
                },
                cancel: function (res) {
                  // 用户取消分享后执行的回调函数
                  logUtil.printLog("取消分享到朋友圈返回的信息为:",res);

                }
              });
              //分享给微信朋友
              wx.onMenuShareAppMessage({
                title: res.data.data.detail.file_name, // 分享标题
                link: str, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                imgUrl: res.data.data.detail.file_image, // 分享图标
                desc: "来自：磁易购"+res.data.data.detail.abstract, // 分享描述
                type: '', // 分享类型,music、video或link，不填默认为link
                dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
                success: function (res) {
                  _this.isShowDataBase =true;
                },
                cancel: function (res) {
                  // 用户取消分享后执行的回调函数
                  logUtil.printLog("取消分享给朋友返回的信息为:",res);
                }
              });
              //分享到QQ
              wx.onMenuShareQQ({
                title: res.data.data.detail.file_name, // 分享标题
                link: str, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                imgUrl: res.data.data.detail.file_image, // 分享图标
                desc: "来自：磁易购"+res.data.data.detail.abstract, // 分享描述
                success: function (res) {
                  _this.isShowDataBase =true;
                },
                cancel: function (res) {
                  // 用户取消分享后执行的回调函数
                  logUtil.printLog("取消分享给QQ好友返回的信息为:",res);
                }
              });

              //分享到QQ空间
              wx.onMenuShareQZone({
                title: res.data.data.detail.file_name, // 分享标题
                link: str, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                imgUrl: res.data.data.detail.file_image, // 分享图标
                desc: "来自：磁易购"+res.data.data.detail.abstract, // 分享描述
                success: function (res) {
                  _this.isShowDataBase =true;
                },
                cancel: function (res) {
                  // 用户取消分享后执行的回调函数
                  logUtil.printLog("取消分享到QQ空间返回的信息为:",res);
                }
              });
            },(err)=>{

            });
          },
        /**
         * 跳转到积分规则的界面
          * @param $event
         */
        goGetJiFenPage($event){
          this.$router.push({path: '/gjifen', replace: true});
          $event.preventDefault();
        },
        /**
         * 获取当前用户的当前可用积分
         */
        getUserJIfen(){

          const _this = this;
          this.$http.get(`${Api.MYTIEZI}${localStorage.getItem('userId')}/credits/`)
            .then((res)=>{
              _this.initJiFenData(res);
            },(err)=>{

            })
        },
       openwin(url) {
         let token = localStorage.getItem('token');
         if(token===null || token===''){
           this.$router.push({path: '/login', replace: true});
         }
         if(this.currentCredit <this.credit){
           this.showMsg('很抱歉，您当前积分不足，不能下载该文件！');
           return;
         }
         const _this = this;
         this.$http.post(Api.FILESDETAIL,{
           file_id:_this.id
         }).then((res)=>{
         },(err)=>{
         });
          var a = document.createElement("a");
          a.setAttribute("href", `${url}?date=${new Date().getTime()}`);
          a.setAttribute("target", "_blank");
          a.setAttribute("id", "openwin");
          document.body.appendChild(a);
          a.click();
     },
        /**
         * 对用户当前积分进行初始化
         * @param res
         */
        initJiFenData(res){
          console.log("返回需要下载的积分信息是:",res);
          if(res.status ===200 && res.data.data!==null) {

            this.currentCredit = res.data.data.detail.current_credit;
          }
        },
        newWin(url) {
          const _this = this;
          this.$http.post(Api.FILESDETAIL,{
            file_id:_this.id
          }).then((res)=>{
           /* let url = localStorage.getItem('url');
            var newTab=window.open('about:blank');
//            this.newWin.location.href = url;
            _this.newWin(url);*/
          },(err)=>{
            _this.showMsg("抱歉，下载失败，请重试!");
          });
          let a = document.createElement('a');
          a.setAttribute('href', url);
          a.setAttribute('target', "_blank");
          a.setAttribute('id', "openwindow");
          // 防止反复添加
          if(!document.getElementById("openwindow")) {
            document.body.appendChild(a);
          }
          a.click();
        },
        /**
         * 下载该文件
         */
        downLoad(){
            if(this.uploader === localStorage.getItem('userId')){
              this.dealFile();
            }else{
              //判断用户当前积分是否够,当前用户与上传者id进行比较(同一个人不扣除积分)
              if(this.currentCredit >=this.credit){
                this.dealFile();
              }else{
                this.showMsg('很抱歉，您当前积分不足，不能下载该文件！');
              }
            }

        },
        s4() {
          return Math.floor((1 + Math.random()) * 0x10000)
          .toString(16)
          .substring(1);
        },
        JsGuid() {
          return this.s4() + this.s4() + '-' + this.s4() + '-' + this.s4() + '-' +
          this.s4() + '-' + this.s4() + this.s4() + this.s4();
        },
        //处理成功以后，在跳转到打开的页面
        dealFile(){
          const _this = this;
          this.$http.post(Api.FILESDETAIL,{
            file_id:_this.id
          }).then((res)=>{
            let url = localStorage.getItem('url');
            var newTab=window.open('about:blank');
//            this.newWin.location.href = url;
            _this.newWin(url);
          },(err)=>{
              _this.showMsg("抱歉，下载失败，请重试!");
          });
        },
        showMsg(str){
          this.$toast({
            message: str,
            duration: 2000,
          });
        },
      },
    /**
     * 当路由离开的时候，移除存储的url
     * @param to
     * @param from
     * @param next
     */
      beforeRouteLeave(to,from,next){
        console.log("哈哈");
        //避免在客户端存在占更大的内存
        localStorage.removeItem('url');
        next();
      }

  }
</script>
<style scoped>
    @import "../../assets/css/datajifendownload.css";
    body {
        background-color: #ffffff;
    }
</style>



